// Request deduplication and caching utility
const requestCache = new Map();
const pendingRequests = new Map();

/**
 * Fetch with request deduplication and timeout
 * Prevents multiple identical requests from running simultaneously
 */
export async function fetchWithCache(url, options = {}) {
  const cacheKey = `${url}_${JSON.stringify(options)}`;
  const cacheTime = 30000; // 30 seconds cache
  const timeout = 15000; // 15 seconds timeout (increased to allow for slow DB connections)

  // Check if we have a cached response
  const cached = requestCache.get(cacheKey);
  if (cached && Date.now() - cached.timestamp < cacheTime) {
    return cached.data;
  }

  // Check if there's already a pending request for this URL
  if (pendingRequests.has(cacheKey)) {
    // Wait for the existing request to complete
    return await pendingRequests.get(cacheKey);
  }

  // Create abort controller for timeout
  const controller = new AbortController();
  let timeoutId;

  // Create the request promise
  const requestPromise = (async () => {
    try {
      // Set timeout
      timeoutId = setTimeout(() => {
        try {
          if (!controller.signal.aborted) {
            controller.abort();
          }
        } catch {
          // Ignore errors during abort
        }
      }, timeout);

      const response = await fetch(url, {
        ...options,
        signal: controller.signal,
      });

      // Clear timeout if request completed successfully
      if (timeoutId) {
        clearTimeout(timeoutId);
        timeoutId = null;
      }

      // Check if response was aborted before parsing
      if (controller.signal.aborted) {
        return { 
          ok: false, 
          data: null, 
          error: 'Request timeout',
          timeout: true 
        };
      }

      const data = await response.json();
      const result = { ok: response.ok, data, response };
      
      // Cache successful responses
      if (response.ok) {
        requestCache.set(cacheKey, {
          data: result,
          timestamp: Date.now(),
        });
      }
      
      return result;
    } catch (error) {
      // Clear timeout if still set
      if (timeoutId) {
        clearTimeout(timeoutId);
        timeoutId = null;
      }
      
      // Handle abort errors (timeouts) gracefully
      if (error.name === 'AbortError' || error.name === 'TimeoutError' || controller.signal.aborted) {
        // Use console.warn instead of console.error for timeouts (expected behavior)
        return { 
          ok: false, 
          data: null, 
          error: 'Request timeout',
          timeout: true 
        };
      }
      
      // Handle other errors
      console.error(`Request failed for ${url}:`, error);
      return { 
        ok: false, 
        data: null, 
        error: error.message || 'Request failed' 
      };
    } finally {
      // Ensure timeout is cleared
      if (timeoutId) {
        clearTimeout(timeoutId);
      }
      // Remove from pending requests
      pendingRequests.delete(cacheKey);
    }
  })();

  // Store the pending request
  pendingRequests.set(cacheKey, requestPromise);

  return requestPromise;
}

/**
 * Clear the cache (useful for testing or forced refresh)
 */
export function clearFetchCache() {
  requestCache.clear();
  pendingRequests.clear();
}
